<script>
/* eslint-disable */
import {closeModal} from "jenesius-vue-modal";
import {USER_LOGIN} from "@/queries/main"
import helperMixin from '@/mixins/helpers'
import MembershipItemsVue from '../small/MembershipItems.vue';
export default {
  mixins:[helperMixin],
  components:{
    MembershipItemsVue
  },
  data() {
    return {
      usernameOrEmail: "",
      password: ""
    }
  },
  methods: {
    async login() {
      if(!this.validateFields()) return 
      try {
        this.showSpinner()
        let { data : {login} } = await this.$apollo
          .mutate({
            mutation: USER_LOGIN,
            variables: {
              addr: "",
              usernameOrEmail: this.usernameOrEmail,
              password: this.password,
            },
          })
        this.hideSpinner()
        if(login.user) {
          const { user, token } = login;
          this.$store.commit("userData", user);
          this.$store.commit("token", token);
          this.$store.commit("loggedIn", true);
          closeModal()
          window.location.reload()
          return
        }
        alert("Please enter valid credentials")
      }catch(e) {
        this.hideSpinner()
        alert(e)
        console.log(e)
      }
    },
    close() {
      closeModal()
    },
    validateFields() {
      if(this.usernameOrEmail.length < 1) {
        alert('Please enter a valid email or username ')
        return false
      }
      if(this.password.length < 1) {
        alert('Please enter a valid password')
        return false
      }
      return true
    }
  }
}
</script>
